<template>
<div class="bottom-color">
  <div class="container-md pages_tab pt-4 pb-2 d-flex justify-content-center">
    <div class="p-2 mt-1 fs-2 border chat_tab col-12 col-lg-8 col-xl-6 rounded-3">
      <img alt="Vue logo" src="../assets/logo.png" class="navbar-brand logo_center">
      即時聊天室
      </div>
  </div>
  <div class="container-md pages_tab pb-4  d-flex justify-content-center">
  <div class="conrainer-sm" v-if="enterW">
      <div class="text-dark">歡迎&ensp;{{msg_W}}&ensp;來到Ms 即時聊天室</div>
      <div class="row justify-content-center">
        <div class="col-lg-12 chatroom min-vh-80 max-vh-80 rounded-3 m-2 overflow-auto" id="msg_box">
          <div>
          <div class="d-flex mt-5 align-items-center" v-if="loading_let">
            <strong class="ms-auto fs-4 loading fw-normal text-warning">聊天室加載中...</strong>
            <div class="spinner-border fs-4 loading ms-3 me-auto text-warning fw-normal" role="status" aria-hidden="true"></div>
          </div>
          <div v-for="(item,index) in enddata" :key="index">
            <div class="msg p-2 m-3">
              <div class="d-flex">
                <div class="fs-5 h-50 w-10 rounded border-2 border msg-W fw-bolder">{{item.Writer.slice(0,1)}}</div>
                <div class="p-1 w-100">
                  <div class="p-0 m-0 text-start color-black text-dark fw-bolder text-nowrap msg-writer ps-1">{{item.Writer}}</div>
                  <div class="text-dark text-start p-0 chat_content mt-2 bg-msg p-2 text-break">{{item.Content}}</div>
                </div>
              </div>
              <div class="">
                <p class="p-0 m-0 text-muted text-end msg-writer">{{item.Time}}</p>
              </div>
            </div>
          </div>
            <div class="h-auto msg-send w-100 m-0 p-0" >
                <div class="fw-bolder d-flex align-items-center justify-content-center p-4 h-auto">
                訊息：
                <textarea id="msg" @keyup.enter="contents_enter(event)" @keydown.down="contents_down"  cols="30" type="text" class="p-1 w-50 h-10" v-model="contents"/>
                <input class="btn btn-primary ms-2 px-2" :disabled="contents.trim() === ''" type="button" value="傳送" @click="ADD_chat(contents,msg_W,this.timers),clear()">
                </div>
            </div>
          </div>
        </div>
      </div>
      </div>
    <div class="text-dark type-w fs-6 chat_tab d-flex align-items-top justify-content-center col-12 col-lg-8 col-xl-6" v-else @keyup.enter="Writers()">
      <div class=" w-80 d-flex align-items-center justify-content-center">
        暱稱：
        <input type="text" ref="input" maxlength="10" size="19" v-model="msg_W"/>
        <input class="btn btn-primary p-1 ms-2 fs-6" :disabled="msg_W.trim() === ''" type="button"  value="確定" @click="Writers()">
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {catch_chat,ADD_chat} from '../db'
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },data(){
    return{
      contents:"",
      writers:"",
      msg_W:"",
      nowTime:"",
      nowDay:"",
      timers:"",
      beforedata:[],
      enddata:[],
      loading_let:true,
      enterW:false,
    }
  },
  methods:{
    contents_enter(){
      if(this.contents.trim() === ''){
        this.clear();
      }else{
        this.ADD_chat(this.contents,this.msg_W,this.timers);
        this.clear();
      }
    },
    contents_down(){
      this.contents= this.contents + "\n";
    }
    ,
    Writers(){
      if(this.msg_W==""){
        this.enterW = false;
      }else{
        this.enterW = true;
        setTimeout(function(){
  let msg_box = document.getElementById('msg_box');
	msg_box.scrollTop=msg_box.scrollHeight;
        }, 100);
      }
    },
    ADD_chat(a,b,c){
      if(this.msg_W==''||this.contents==''){
        alert('請輸入內文及暱稱')
        return
      }else{
        ADD_chat(a,b,c);
        setTimeout(function(){
  let msg_box = document.getElementById('msg_box');
	msg_box.scrollTop=msg_box.scrollHeight;
        }, 500);
      }
    },
    clear(){
      this.contents='';
    },
    catch_d(){
      this.beforedata=catch_chat();
      this.beforedata.sort(this.different);
      this.enddata = this.beforedata.slice(-100)
      if(this.enddata==''){
        this.loading_let= true;
      }else{
        this.loading_let= false;
      }
    },
    different(a,b){
      const TimeA = a.Time
      const TimeB = b.Time
      let comparison = 0;
      if (TimeA > TimeB) {
        comparison = 1;
      } else if (TimeA < TimeB) {
        comparison = -1;
      }
      return comparison;
    },
    // 得到當下時間
    timeFormate(timeStamp) {
        let newdate = new Date(timeStamp);
        let week_list = ['日', '一', '二', '三', '四', '五', '六'];

        let year  = newdate.getFullYear();
        let month = newdate.getMonth()+1  < 10? "0" + (newdate.getMonth()+1): newdate.getMonth() + 1;
        let date  = newdate.getDate() < 10? "0" + newdate.getDate(): newdate.getDate();
        let week = newdate.getDay()
        let hh    = newdate.getHours() < 10? "0" + newdate.getHours(): newdate.getHours();
        let mm    = newdate.getMinutes() < 10? "0" + newdate.getMinutes(): newdate.getMinutes();
        let ss    = newdate.getSeconds() < 10? "0" + newdate.getSeconds(): newdate.getSeconds();

        this.nowTime = hh+":"+mm + ":" + ss;
        this.nowDay = year + "年" + month + "月" + date +"日"+"("+week_list[week]+")";
        this.timers = this.nowDay+this.nowTime;
     },
      // 定時器函數
      nowTimes(){        
        let self = this;
        self.timeFormate(new Date());
        setInterval(function(){
          self.timeFormate(new Date());
        }, 1000);
        setInterval(function(){
          self.catch_d();
        }, 500);
      }
  },created(){
    this.nowTimes();
  },
  mounted(){

  }
}

</script>
