<template>
<div class="bottom-color">
  <div class="container-md pages_tab py-4 min-vh-80">
    <div class="d-flex mt-3 align-items-center" v-if="loading_let">
      <strong class="ms-auto loading">Loading...</strong>
      <div class="spinner-border loading ms-3 me-auto" role="status" aria-hidden="true"></div>
    </div>
    <div v-else>
      <div class="row my-5 mx-auto p-3 border rounded border-dark max-vw-75" v-for="(item,index) in 10" :key="index">
        <div class="col">
          <div class="m-1 fs-3">{{title[index]}}</div>
          <div class="my-3 overflow-auto text-break news text-start">{{content[index]}}</div>
          <img :src="urlImg[index]" class="max-vw-75">
          <div class="m-1">{{time[index]}}</div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>
<script>

export default {
  data(){
    return{
      title:[],
      urlImg:[],
      content:[],
      time:[],
      description:[],
      loading_let:true,
  }
},
methods:{
  news(){
    if (this.content[8]==undefined){
      this.loading_let = true
    }else{
      this.loading_let = false
    }
  }
  },
mounted(){
let url = 'https://gnews.io/api/v4/top-headlines?token=d662a71691d85369f476be7f48168b7c&lang=zh&country=tw'
          
let req = new Request(url);

fetch(req)
    .then((response) =>
    response.json()
    )
    .then((data) =>
    {
    for (let i=0; i<10;i++){
    this.time.push(data.articles[i].publishedAt)
    this.time[i] = this.time[i].replace("T"," ")
    this.time[i] = this.time[i].replace("Z"," ")
    this.title.push(data.articles[i].title)
    this.urlImg.push(data.articles[i].image)
    this.content.push(data.articles[i].content)
    }
    })
    let self = this;
    setInterval(function(){
    self.news();
    }, 500);
}}
</script>
<style>
  .max-vw-75{
    max-width: 90%;
  }
    .loading{
    height: 7vh;
    min-width: 7vh;
    margin-top: 20vh ;
    margin-bottom: 20vh ;
    font-size: 7vh;
    line-height: 7vh;
    color:#4e95db;
  }
  .news{
  overflow: hidden;
  display: -webkit-box;
  text-overflow:ellipsis;
  -webkit-box-orient: vertical;
  white-space: pre-wrap;
  }
</style>