<template>
  <div class="bottom-color">
  <div class="pages_tab container-md min-vh-80 p-3 pt-4 position-relative">
      <h1>文章列表</h1>
      <div>
        標題：
        <input maxlength="20" type="text" v-model="titles"/>
      </div>
      <div class="fs-6 d-flex justify-content-center align-items-center">
        <div>
        內文：
        </div>
        <textarea class="m-1 p-1 fs-6" rows="2" cols="19" type="text" v-model="contents"/>
      </div>
      <div>
        筆名：
        <input maxlength="10" size="19" type="text" v-model="writers"/>
      </div>
        <input class="btn btn-primary mt-3" type="button" value="送出資料" :disabled="titles==''||contents==''||writers==''" @click="ADD_fb(titles,contents,writers,this.timers),clear()">
      <div>
        <div class="d-flex mt-3 align-items-center" v-if="loading_let">
          <strong class="ms-auto loading">Loading...</strong>
          <div class="spinner-border loading ms-3 me-auto" role="status" aria-hidden="true"></div>
        </div>
      </div>
      <div class="row my-5 mx-auto">
      <div v-for="(item,index) in enddata" class="col-xs-12 col-sm-6 col-md-4 col-xl-3" :key="index">
          <div class="my-5 mx-auto h-75 card p-2" data-bs-toggle='modal' :data-bs-target='"#staticBackdrop"+index' v-if="now_page==Math.ceil((index+1)/20)" >
            <div class="p-3">
              <div>
                <h3 class="text-warning text-start pb-3 title_ty fw-bolder">
                No.{{index+1}}&ensp;{{item.Title}}
                </h3>
              </div>
              <p class="text-dark text-start p-0 hid_font4">{{item.Content}}</p>
            </div>
            <div class="d-flex mb-0 mt-auto">
              <div class="ms-auto mb-0 ">
                <p class="m-0 text-start fw-bolder text-end wt_color">Ms筆名：{{item.Writer}}</p>
                <p class="ms-auto mb-1 text-end wt_color">{{item.Time}}</p>
              </div>
            </div>
          </div>
        <div class="modal fade outside_content" :id="'staticBackdrop'+index" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" :aria-labelledby="'staticBackdropLabel'+index" aria-hidden="true">
          <div class="modal-dialog modal-lg modal-dialog-scrollable">
            <div class="modal-content story_content">
              <div class="modal-header border-0 border-primary">
                <h5 class="modal-title text-warning fw-bolder fs-1" :id="'staticBackdropLabel'+index">No.{{index+1}}&emsp;{{item.Title}}</h5>
                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
              </div>
              <div class="modal-body text-dark text-start pb-0 white-space">
                {{item.Content}}
                <div class="d-flex mb-0">
                  <div class="ms-auto mb-0">
                    <p class="m-0  fw-bolder wt_color text-end">Ms筆名：{{item.Writer}}</p>
                    <p class="m-0 wt_color text-end">{{item.Time}}</p>
                  </div>
                </div>
              </div>
              <div class="modal-footer border-0 border-primary">
                <button type="button" class="btn btn-danger me-auto" @click="delet_(item.Id)" data-bs-dismiss="modal">刪除文章</button>
                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">關閉文章</button>
                <button type="button" class="btn btn-primary">還在想的按鈕</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
    <nav aria-label="Page navigation example">
      <ul class="pagination justify-content-center">
        <li class="page-item">
          <button class="border-start border-top border-bottom pagelink rounded-start" @click="page_change(1)" aria-label="Previous">
            <span aria-hidden="true">First</span>
          </button>
        </li>
        <li class="page-item" v-for="(item,pages) in Math.ceil(this.enddata.length/20)" :key="pages.id"><button @click="page_change(pages+1)" class="border pagelink">{{pages+1}}</button></li>
        <li class="page-item">
          <button class="border-end border-top border-bottom pagelink rounded-end" @click="page_change(Math.ceil(this.enddata.length/20))" aria-label="Next">
            <span aria-hidden="true">Last</span>
          </button>
        </li>
      </ul>
    </nav>
    <div class="text-end pb-2 me-2 position-absolute bottom-0 end-0">{{timers}}</div>
  </div>
  </div>
</template>
<script>
import { ADD } from '../db'
import {catch_data,delet_id} from '../db'

export default ({
  data() {
    return {
      documents: [],
      titles:"",
      contents:"",
      writers:"",
      nowTime:"",
      nowDay:"",
      timers:"",
      beforedata:[],
      enddata:[],
      loading_let:true,
      now_page:"1",
    }
  },
  methods:{
    page_change(page){
      this.now_page=page;
      window.scrollTo(0,0);
    },
    delet_(id){
    let yes = confirm('你確定刪除此文章嗎？');
    if (yes) {
      delet_id(id)
      alert('已刪除此文章');
      }
    },
    ADD_fb(titles,contents,writers,timers){
      ADD(titles,contents,writers,timers);
    },clear(){
      this.titles=''
      this.contents=''
      this.writers=''
    },
    catch_d(){
      this.beforedata=catch_data();
      this.beforedata.sort(this.different)
      this.enddata = this.beforedata.reverse();
      if(this.enddata==''){
        this.loading_let= true;
      }else{
        this.loading_let= false;
      }
    },
    different(a,b){
      const TimeA = a.Time
      const TimeB = b.Time
      let comparison = 0;
      if (TimeA > TimeB) {
        comparison = 1;
      } else if (TimeA < TimeB) {
        comparison = -1;
      }
      return comparison;
    },
    // 得到當下時間
    timeFormate(timeStamp) {
        let newdate = new Date(timeStamp);
        let week_list = ['日', '一', '二', '三', '四', '五', '六'];

        let year  = newdate.getFullYear();
        let month = newdate.getMonth()+1  < 10? "0" + (newdate.getMonth()+1): newdate.getMonth() + 1;
        let date  = newdate.getDate() < 10? "0" + newdate.getDate(): newdate.getDate();
        let week = newdate.getDay()
        let hh    = newdate.getHours() < 10? "0" + newdate.getHours(): newdate.getHours();
        let mm    = newdate.getMinutes() < 10? "0" + newdate.getMinutes(): newdate.getMinutes();
        let ss    = newdate.getSeconds() < 10? "0" + newdate.getSeconds(): newdate.getSeconds();

        this.nowTime = hh+":"+mm + ":" + ss;
        this.nowDay = year + "年" + month + "月" + date +"日"+"("+week_list[week]+")";
        this.timers = this.nowDay+this.nowTime;
     },
      // 定時器函數
      nowTimes(){        
        let self = this;
        self.timeFormate(new Date());
        setInterval(function(){
          self.timeFormate(new Date());
        }, 1000);
        setInterval(function(){
          self.catch_d();
        }, 500);
      },
  },
  created(){
    this.nowTimes();
  }
})

</script>
<style>
  .card{
    border: 1px solid rgb(255, 224, 196);
    background-color:rgba(255, 255, 236, 0.908) ;
    box-shadow: 0px 0px 15px 5px rgb(255, 226, 201);
  }
  .title_ty{
    color:rgb(161, 161, 161) ;
    border-bottom:1px rgb(255, 224, 196) solid ;
    text-shadow: 2px 2px 5px 5px black;
    font-size: 20px;
  }
  .card:hover{
    cursor: pointer;
    background-color:rgba(255, 255, 179, 0.908) ;
    transition: .5s linear background-color;
    transition: .3s linear transform;
    transform: translateY(-10px)  ;
  }
  .wt_color{
    color: #f7b8b8;
    font-size: 13px;
  }
  .story_content{
    background: #fffdcf;
  }
  .outside_content{
    background: url("../assets/back.png");
  }
  .hid_font4 {
  overflow: hidden;
  display: -webkit-box;
  text-overflow:ellipsis;
  -webkit-line-clamp: 4; /*行數*/
  -webkit-box-orient: vertical;
  white-space: pre-wrap;
}
.hid_font1 {
  overflow: hidden;
  display: -webkit-box;
  text-overflow:ellipsis;
  -webkit-line-clamp: 1; /*行數*/
  -webkit-box-orient: vertical;
  white-space: pre-wrap;
}
  .loading{
    height: 7vh;
    min-width: 7vh;
    margin-top: 20vh ;
    margin-bottom: 20vh ;
    font-size: 7vh;
    line-height: 7vh;
    color:#4e95db;
  }
  .white-space {
  overflow: hidden;
  display: -webkit-box;
  text-overflow:ellipsis;
  -webkit-box-orient: vertical;
  white-space: pre-wrap;
}
  .page-link:focus{
    box-shadow: none;
    background-color: none;
  }
    .page-link:active{
    background-color:red ;
  }
  .pagelink{
    padding: 7px;
    background-color: #fff;
    color: black;
  }
  .pagelink:hover{
    background-color: #8e8c8c;
  }

</style>